

import './index.scss'
import Head from '~/components/Head'



export default class Notice extends Component{
  constructor(){
    super();
    this.state={
      img:require('@/assets/images/notice.jpg')
    }
  }
  render(){
    return(
      <div className='storenotice'>
        <Head title='公告' showBack={true}></Head>
        <div className='notice'>
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            <img src={this.state.img} style={{ width: '95%', height: '200px' }} src={this.state.img} alt=""/>
          </div>
          <div style={{ padding: '5px', lineHeight: '24px' ,color:'red'}}>
            <p style={{ textIndent: '24px' }}>1. 严禁在图书馆内吸烟。</p>
            <p style={{ textIndent: '24px' }}>2. 保持室内整洁，禁止将食物及饮料带入馆内；请勿随意搬拽阅览桌椅。</p>
            <p style={{ textIndent: '24px' }}>3. 保持室内安静：入馆请将呼机和手机关机或调为静音状态；在馆内请轻声交谈，以免影响他人阅读。</p>
            <p style={{ textIndent: '24px' }}>4. 尊重管理人员，做个文明读者。</p>
            <p style={{ textIndent: '24px' }}>5. 爱护书刊资料及一切公共财物，请勿涂抹、撕毁、私藏书刊。</p>
          </div>
        </div>
      </div>
    )
  }
}